import request from '@/static/request.js'
export default {
  data() {
    return {
      id: '',
      photo: '未填写',
      name: '未填写',
      wechat: '未填写',
      telephone: '未填写',
      birthday: '未填写',
      sex: '未填写',
      email: '未填写',
    }
  },
  mounted() {
    let vm = this;
    request
      .get('schoolmateData/', {
        schoolmate_id: vm.id,
      })
      .then(res => {
        vm.name = res.data[0].user_name
        if (res.data[0].user_gender === 'm') {
          vm.sex = '男'
        } 
        else {
          vm.sex = '女'
        }
        vm.telephone = res.data[0].user_telephone
        vm.email = res.data[0].user_email
        vm.wechat = res.data[0].user_weixinnumber
      })
  },
  methods: {
    toLogin() {
      uni.reLaunch({
        url: '../login/login'
      });
    },
    toEdit() {
      uni.navigateTo({
        url: '../edit/edit'
      });
    }
  },
  onLoad(options) {
    uni.getStorage({
      key: 'GetId',
      success: res => {
        console.log(res);
        let self = this;
        self.id = res.data;
      }
    })
  },
};
